.auth-page {
  min-height: calc(98vh - 70px);
}
.auth-page-card {
  padding: 2em;
  border-radius: 8px;
  padding-top: 4em;
  padding-bottom: 4em;
}
.auth-card-title {
  font-weight: 600;
}
.auth-card-subtitle {
  color: #979797;
}
.vertical-divider {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  margin: 0;
  padding: 0;
  width: auto;
  height: 50%;
  line-height: 0;
  text-align: center;
  text-transform: uppercase;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.vertical-divider:before,
.vertical-divider:after {
  position: absolute;
  left: 50%;
  content: "";
  z-index: 9;
  border-left: 1px solid rgba(34, 36, 38, 0.15);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  width: 0;
  height: calc(100% - 1rem);
}

.\--with-divider > .vertical-divider {
  height: calc(50% - 1rem);
}

.vertical-divider:before {
  top: -100%;
}

.vertical-divider:after {
  top: auto;
  bottom: 0;
}

.\--with-divider {
  position: relative;
}

.\--with-divider > [class^="col-"],
.\--with-divider > [class*=" col-"] {
  padding-left: 30px; /* gutter width (give a little extra room) 2x default */
  padding-right: 30px; /* gutter width (give a little extra room) 2x default */
}
.form-row {
  margin: 0;
}
.form-check {
  padding-left: 0;
}
.social-button-facebook {
  background: #3a5999;
  border-radius: 4px;
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 1em;
  margin-top: 1em;
  text-align: left;
  padding: 0.65em 1em;
}
.social-button-twitter {
  background: #45b0e4;
  border-radius: 4px;
  color: #ffffff;
  margin-bottom: 1em;
  margin-top: 1em;
  text-align: left;
  text-transform: uppercase;
  padding: 0.65em 1em;
}
.social-button-google {
  background: #de4c33;
  border-radius: 4px;
  color: #ffffff;
  margin-bottom: 1em;
  margin-top: 1em;
  text-align: left;
  text-transform: uppercase;
  padding: 0.65em 1em;
}
.social-button-linkedin {
  background: #007aaa;
  border-radius: 4px;
  color: #ffffff;
  margin-bottom: 1em;
  margin-top: 1em;
  text-align: left;
  text-transform: uppercase;
  padding: 0.65em 1em;
}
.social-buttons i {
  margin-right: 2em;
  margin-left: 2em;
}
.social-button-facebook:hover,
.social-button-google:hover,
.social-button-linkedin:hover,
.social-button-twitter:hover {
  color: #ffffff;
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}
.auth-page-link {
  color: #00b0f0;
}
.auth-page-link:hover {
  color: #333333;
  text-decoration: none;
}
.authy {
  min-height: 100vh;
  display: flex;
  width: 100vw;
}
@media screen and (max-width: 800px) {
  .auth-page-card {
    /*overflow-x: hidden;*/
  }
  .vertical-divider {
    display: none;
  }
  .social-buttons {
    border-top: 2px solid #333333;
  }
  .auth-card-title {
    font-size: 1.8rem;
  }
}
@media (max-width:900px) and (min-width:400px) {
  .auth-page-card {
    margin: 0 1em !important;
  }
}
.auth-bg-overlay {
  background: rgba(51, 51, 51, 0.9);
  min-height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}
.auth-bg {
  overflow: hidden;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
}

.authBg {
  display: flex;
  justify-content: center;
  align-content: center;
}
.authBg img {
  margin: auto;
  width: auto;
  height: 100%;
}
.upload-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 996;
}
.modal-main {
  position: fixed;
  background: #ffffff;
  max-width: 90%;
  min-width: 50%;
  height: 400px;
  top: 50%;
  left: 50%;
  padding: 3em;
  border-radius: 4px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
}
.opa-0 {
  opacity: 0;
}
.opa-1 {
  opacity: 1;
}
.btn-uplaod-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin: 1em;
  height: 10em;
  width: 10em;
  overflow: hidden;
}
.btn-upload {
  border: 2px dashed #00b0f0;
  color: #00b0f0;
  background-color: white;
  padding: 8px 20px;
  border-radius: 50%;
  font-size: 20px;
  font-weight: bold;
  height: 10em;
  width: 10em;
}
.btn-upload-wrapper input[type="file"] {
  font-size: 2.2em;
  text-align: center;
  line-height: 7em;
  position: absolute;
  /* margin: auto; */
  left: 0;
  top: 0;
  opacity: 0;
}
.btn-upload-wrapper input[type="file"]:hover {
  cursor: pointer;
}
.btn-upload-wrapper input[type="file"]:hover ~ .btn-upload,
.btn-upload:hover {
  background-color: #00b0f0;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}

.page-title {
  padding: 0.5em 1.5em;
}
.flex-1 {
  flex: 1 1;
}
.floating-edit-button-wrapper {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #00b0f0;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.12);
  position: absolute;
  top: 10%;
  right: 5%;
  border: none;
  display: flex;
}
.floating-edit-button-wrapper.\--profile-picture {
  top: 80%;
  left: 70%;
}
.floating-edit-button-wrapper svg {
  margin: auto;
}
.floating-edit-button-wrapper:hover {
  box-shadow: 3px 6px 6px rgba(0, 0, 0, 0.12);
  cursor: pointer;
}
.friends-list-container {
  border-radius: 8px;
  background: #ffffff;
  flex: 1 1;
  align-self: flex-start;
}
.friends-list-header {
  padding: 1em 1em;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.friends-list-header h5,
.friends-list-header p {
  margin-bottom: 0.2em;
}
.friends-search-container {
  padding: 0.5em;
  background: #f1f1f1;
}

.friends-search-container .searchContainer {
  display: inline-flex;
  border: 1px solid #c4c4c4;
  border-radius: 4px;
  overflow: hidden;
  max-height: 40px;
  width: 90%;
  align-items: center;
}
.friends-search-container.chatbox .searchContainer {
  margin: 0.6em 1em;
}
.searchIconContainer {
  background: #f1f1f1;
  margin: 0;
  height: 100%;
}
.searchIcon {
  padding: 0.8rem;
  font-size: 16px;
}

.searchBox {
  border: 0;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  flex: 1 1;
}
.friends-list {
  padding: 1em;
}
.notifications-container {
  background: #ffffff;
  border: 1px solid #f1f1f1;
  border-radius: 4px;
  width: 100%;
}
.notifications-page-header {
  padding: 0.5em 1em;
  border-bottom: 1px solid #f1f1f1;
}
.single-notification {
  display: flex;
  padding: 1em 2em;
  justify-content: space-between;
  border-bottom: 1px solid #f1f1f1;
}
.notification-owner-wrapper {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 15px;
}
.notification-owner {
  width: 100%;
  height: 100%;
}
.single-notification-link, 
.single-notification-link:hover, 
.single-notification-link:visited, 
.single-notification-link:active {
  color: inherit !important;
  display: flex;
  text-decoration: none;
}
.chat-list {
  background: #fff;
  overflow: auto;
  height: calc(100vh - 255px);
}
.chat-head {
  padding: 16px;
  border: 1px solid #F0F0F0;
  display: flex;
}
.chat-head-photo {
  height: 40px;
  width: 40px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.chat-head-photo > img {
  width: 100%;
  height: 100%;
}
.chat-head-content {
  width: calc(100% - 50px);
}
.chat-head-user-and-time {
  display: flex;
  justify-content: space-between;
}
.chat-head p {
  margin-bottom: 2px;
}
.previous-chat {
  overflow: hidden;
  width: 80%;
  text-overflow: ellipsis;  
  white-space: nowrap;
}
.chat-head-active, .chat-head:hover {
  background: #E8F6FB;
  cursor: pointer;
}
.chat-head a {
  text-decoration: none;
}
.message-count {
  color: #fff;
  background: #09526D;
  border-radius: 2px;
  font-size: 12px !important;
  padding-left: 4px;
  padding-right: 4px;
  height: 18px;
  min-width: 18px;
  text-align: center;
}
.chat-header {
  display: flex;
  padding: 16px;
  border: 1px solid #F0F0F0;
}
.main-chat-container {
  background: #fff;
  height: calc(100vh - 120px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.close-chat {
  background: transparent;
  border: none;
  font-weight: 800;
  cursor: pointer;
}
.chat-interface {
  padding: 16px;
  flex: 1 1;
  overflow: auto;
  flex-direction: column;
  padding-bottom: 0;
}
.chat-message-wrapper {
  display: flex;
  padding-bottom: 16px;
}
.chat-message-wrapper.owner {
  justify-content: flex-end;
} 
.message-owner {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  align-self: flex-end;
  margin-right: 12px;
  margin-bottom: 8px;
}
.message-owner img {
  width: 100%;
  height: 100%;
}
.message-wrapper {
  max-width: 300px;
}
.message-wrapper p {
  margin-bottom: 0;
}
.other-chat-message {
  background:  #E1E1E1;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner-chat-message {
  background:  #DBEDF4;;
  border-radius: 6px;
  padding: 0.5em 1em;
}
.owner .message-owner {
  margin-left: 12px;
  margin-right: 0;
}
.message-time {
  text-align: right;
}
.chat-input-wrapper {
  padding: 1em;
  background: #fafafa;
  margin: 0 -16px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}
.chat-input-wrapper .form-control {
  flex: 1 1;
}
.chat-input-wrapper .btn {
  margin: 0;
  padding: 0.5em 1em;
  margin-left: 1em;
}
.countdown {
  border-radius: 8px;
}
.countdown>h3, .countdown>p {
  font-weight: 900;
}
